main {
  height: 85.8%;
  padding-top: 64px;
}

.search_box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 7.3%;
  min-height: 53px;
}

.search_inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 88%;
  height: 50%;
  background-color: #f1f2f4;
  border-radius: 5px;

  > i {
    font-size: 16px;
    color: #bebcc1;
    align-self: center;
    margin-left: 15px;
  }

  > input {
    width: 90%;
    background: transparent;
    border: none;
    padding: 0 15px;
  }

  &::placeholder {
    text-align: center;
  }
}

.idx_ri {
  display: flex;
    justify-content: space-between;
    margin-left: 53px;
}

/*main_section*/
.main_section {
   header {
    height: 32px;
    line-height: 32px;
    padding-left: 15px;
    border: 1px solid #ebebeb;
    border-left: none;
    border-right: none;
    color: #b6b5b1;
  }

  > ul {
    display: flex;
    flex-direction: column;
  }
 
  a {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 16px;
 }
}
.prof_in {
    overflow: hidden;
    display: block;
    width: 40px;
    height: 40px;
    margin: 10px 15px;
    border-radius: 50%;
}

 .profile_name {
  flex: auto;
}

.profile_messages {
  height: 30px;
  line-height: 30px;
  margin-right: 15px;
  padding: 0 10px;
  border-radius: 5px;
  background: #f0f0f0;
  font-size: 10px;
  color: #8e9399;
}


