/*main css*/
@import url(reset.scss);
@import url(Header.scss);
@import url(Nav.scss);

body {
  font-size: 12px;
}

a:link,
a:visited {
  color: #000;
  text-decoration: none;
}

a:hover,
a:focus,
a:active {
  color: #000;
  text-decoration: none;
}

img {
  max-width: 100%;
}
.profile_img, .chats_img {
  overflow: hidden;
    display: block;
    width: 40px;
    height: 40px;
    margin: 10px 15px;
    border-radius: 50%;
}

.empty {
  display: block;
  width: 40px;
  height: 40px;
  margin: 10px 15px;
  border-radius: 50%;
  background: url(../images/empty.jpg) 0 0 no-repeat;
  background-size: cover;
}