.authContainer{
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: white;
  .authImg{
      img{
          margin-bottom: 60px;
      }
  }
  .container{ 
      display: flex;
      flex-direction: column;
      width: 100%; 
      max-width: 320px;  
      .authInput{
          max-width: 320px;
          margin-bottom: 10px;
          padding: 10px;
          border-radius: 9px;
          background-color: rgba(255,255,255,1);
          font-size: 12px;
          color: #000;
          border: 1px solid #513736;
          &.authSubmit{
              margin-top: 10px;
              background: rgb(243, 243, 93);
              color: black;
              text-align: center;
          }
      }
      .authError{
          color: tomato;
          text-align: center;
          font-weight: 500;
          font-size: 12px;
      }
  }
  .authSwitch{
      display: block;
      color: #513736;
      margin-top: 30px;
      margin-bottom: 40px;
      font-size: 12px;
      text-decoration: underline;
      cursor: pointer;
  }
  .authBtns{
      display: flex;
      justify-content: space-between;
      width: 100%;
      max-width: 320px;
      .authBtn{
          cursor: pointer;
          width: 150px;
          border-radius: 20px;
          border: none;
          padding: 10px 0;
          background: #fff;
          font-size: 12px;
          svg{
              margin-left: 3px;            }
      }
  }
}